body {
  margin: 0;
  padding: 0;
}

div {
  display: inline-block;
  margin: 0;
}

a {
  text-decoration: none;
}

.app {
  width: 100%;
  width: 450px;
  height: 350px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 18px 10px;
  box-sizing: border-box;
  border-bottom: 1px solid lightgrey;
}

h1 {
  font-family: Raleway;
  font-size: 17px;
  color: #2867B2;
  margin: 0;
}

.main {
  padding: 18px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.button {
  display: inline-block;
  padding: 0.8em 2em;
  border-radius: 2em;
  box-sizing: border-box;
  text-decoration: none;
  font-size: 16px;
  font-family: 'Roboto',sans-serif;
  font-weight: 300;
  color:#FFFFFF;
  background-color: #4eb5f1;
  text-align: center;
  transition: all 0.2s;
  cursor: pointer;
}

.button:hover{
  background-color:#4095c6;
}

.popup-text {
  /* text-align: center; */
  font-size: 14px;
  margin: 15px 0;
}

.error {
  color: rgb(161, 41, 41);
  font-weight: 600;
}

.results-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 15px;
}

h2 {
  text-align: center;
}

.name-text {
  color: #2867B2;
}

.company-text {
  color: green;
}

.email-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.email-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 120px;
}

.email-buttons > div, .email-buttons > a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  background-color: white;
  border-radius: 10%;
  border: 1px solid lightgrey;
  cursor: pointer;
}

.email-buttons > div:hover, .email-buttons > a:hover {
  background-color: rgb(148, 148, 148);
}

.popup {
  position: fixed;
  top: 50%;
  left: 28%;
  padding: 1rem;
  font-size: 1rem;
  background-color: black;
  color: white;
}